#root {
   padding:0 10px;
   margin:0 auto;
   box-sizing:border-box;
}
html {
   background-color:white;
}
.uk-label {
   width:50px;
   vertical-align:1px;
   position:absolute;
   top:8px;
   left:10px;
   font-family:'Space Mono', monospace;
}
.uk-container {
   font-size:1.1em;
   overflow-y:auto;
}
#media-container {
   position:relative;
   height:54vh;
   background:radial-gradient(white, #fafafa);
   margin-bottom:5px;
   box-sizing:border-box;
   padding:0;
   border-width:5px;
   border-color:#555533;
   border-style:double;
   text-align:center;
   overflow:hidden;
   touch-action:none;
}
#steps-container {
   height:42vh;
   padding-left:5px;
   padding-right:5px;
   padding-top:3px;
   overflow-y:scroll;
}
#steps-container::-webkit-scrollbar {
   width:8px;
   box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
   border-radius:5px;
}
#steps-container::-webkit-scrollbar-thumb {
   background-color:#aaccdd;
   border-radius:5px;
}
.uk-card {
   min-height:12vh;
   border-top-right-radius:5px;
   border-bottom-right-radius:5px;
   cursor:pointer;
   box-shadow:0 0 3px #aaa;
   text-align:justify;
   color:black;
   font-family:'Rubik', sans-serif;
   font-size:1em;
}
.step-text {
   margin-bottom:0px;
   margin-left:-10px;
}
.MathJax:focus, .mjx-chtml:focus, .MathJax_SVG:focus {
   outline:none;
}
.uk-button {
   border-radius:2px;
   margin-bottom:10px;
   font-family:'Space Mono', monospace;
}
.arrow, .reparrow {
   position:absolute;
   display:flex;
   align-items:center;
   justify-content:center;
   width:3vh;
   height:3vh;
   top:5px;
   right:5px;
   box-shadow:0 0 2px #ccc;
   border-radius:2px;
   opacity:0.8;
   box-sizing:border-box;
   padding:15px;
}
.reparrow {
   right:calc(3vh + 17px);
   padding-top:17px;
   padding-bottom:13px;
}
.clckpnt {
   cursor:pointer;
}
.coord-input {
   width:35%;
   height:100%;
   border-radius:2px;
   border:none;
   outline:none;
   background-color:darkgreen;
   color:white;
   text-align:center;
   font-family:sans-serif;
   font-size:1.1em;
   padding-left:5px;
   text-align-last:center;
}
.coord-pair {
   min-width:40%;
   min-height:15%;
   position:absolute;
   opacity:0;
}
#reflectQuestion {
   border-top-left-radius:4px;
   border-bottom-left-radius:4px;
   font-family:'Rubik', sans-serif;
}
#soundicon {
   position:absolute;
   bottom:14px;
   right:5px;
   width:4vh;
   height:4vh;
   cursor:pointer;
}
.sidenav {
  height:100%;
  width:0;
  position:fixed;
  z-index:1;
  top:0;
  left:0;
  background-color:#4499cc;
  overflow-x:hidden;
  overflow-y:scroll;
  padding-top:60px;
  animation-timing-function:ease-in-out;
  animation-duration:0.5s;
}
.sidenav span {
  padding:8px 8px 8px 32px;
  text-decoration:none;
  color:#818181;
  display:block;
}
.sidenav span:hover {
  color:#f1f1f1;
}
.sidenav .closebtn {
  position:absolute;
  top:0;
  right:25px;
  font-size:36px;
  margin-left:50px;
  cursor:pointer;
}
@media screen and (max-height: 450px) {
  .sidenav {padding-top:15px;}
  .sidenav a {font-size:18px;}
}
#drawerToggle {
   position:fixed;
   display:flex;
   align-items:center;
   justify-content:center;
   top:10%;
   left:0;
   height:5vh;
   width:4vh;
   cursor:pointer;
   z-index:1;
   box-shadow:0 0 3px #ccc;
   border-top-right-radius:3px;
   border-bottom-right-radius:3px;
   font-size:1.5em;
   padding-bottom:5px;
}
.sideImg {
   display:block;
   margin-top:6%;
   margin-left:8%;
   width:75%;
   box-shadow:0 0 3px #fff;
}
figcaption {
   color:white;
   font-size:0.8em;
   margin-top:5px;
   margin-left:8%;
}
#interactive {
   height:100%;
   width:100%;
}
